body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color:#476072 ;
  font-family: 'Architects Daughter', cursive;
  overflow: hidden;
}


/* desktop size */
@media screen and (min-width:1024px) {
  .contact__container {
    background: -webkit-linear-gradient(90deg, #334257 10%, #476072 70%);
    width: 100%;
    height: 250px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .contact_items {
    /* background:  -webkit-linear-gradient(90deg, #476072 10%, #334257 70% ); */
    width: 100%;
    /* height: 80px; */
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    /* box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75); */
  }

  .email_contact_section,
  .phone_contact_section,
  .whatsapp_contact_section {
    width: 100%;
    color: #EEEEEE;
    font-weight: bolder;
    font-size: 2vw;
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
    /* animation: tracking-in-contract-bck 4s cubic-bezier(0.215, 0.610, 0.355, 1.000) both infinite; */

  }

  .phone_text {
    text-decoration: none;
    color: #EEEEEE;
    display: flex;
    align-items: center;
    cursor: pointer;
  }

  .email-icon-container {
    cursor: pointer;
  }

  .email-icon-footer,
  .phone-icon-footer,
  .whatsapp-icon-footer {
    margin-left: 10px;
  }


  @-webkit-keyframes tracking-in-contract-bck {
    0% {
      letter-spacing: 1em;
      -webkit-transform: translateZ(400px);
              transform: translateZ(400px);
      opacity: 0;
    }

    40% {
      opacity: 0.6;
    }

    100% {
      -webkit-transform: translateZ(0);
              transform: translateZ(0);
      opacity: 1;
    }
  }


  @keyframes tracking-in-contract-bck {
    0% {
      letter-spacing: 1em;
      -webkit-transform: translateZ(400px);
              transform: translateZ(400px);
      opacity: 0;
    }

    40% {
      opacity: 0.6;
    }

    100% {
      -webkit-transform: translateZ(0);
              transform: translateZ(0);
      opacity: 1;
    }
  }

  .logo_contact_section {
    width: 20%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
  }
}

