/* desktop size */
@media screen 
  and (min-width: 1024px){
    .app {
      background-color: #334257;
        width:80%;
        min-height: 200px;
        height: -webkit-min-content;
        height: min-content;
        border-radius: 15px;
        padding: 50px;
        box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
        display: flex;
        justify-content: space-evenly;
    
      }
    .score_section {
      width: 100%;
      height: -webkit-min-content;
      height: min-content;
      min-height: 200px;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      font-size: 4vh;
      color: white;
      }
      
      /* QUESTION/TIMER/LEFT SECTION */
      .question-section {
        width: 100%;
        position: relative;
        color: #ffffff;
        font-weight: bolder;
      }
      
      .question-count {
        margin-bottom: 20px;
        font-size: 1.5vw;
      }
      
      .question-count span {
        font-size: 3vw;
      }
      
      .question-text {
        margin-bottom: 12px;
        font-size: 1.5vw;
      }
      
      /* ANSWERS/RIGHT SECTION */
      .answer-section {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
      }
      
      button {
        width: 70%;
        font-size: 1.5vw;
        color: white;
        background-color: #476072;
        border-radius: 10px;
        display: flex;
        padding: 5px;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        border: none;
      }
      
      .correct {
        background-color: #2f922f;
      }
      
      .incorrect {
        background-color: #ff3333;
      }
      
      button:hover {
        background-color: #334257;
        box-shadow: 1px 1px 1px #548CA8;
      }
      
      button:focus {
        outline: none;
      }
      
      button svg {
        margin-right: 5px;
      }
  }


